<template>

  <div id="bread-container">
    <div id="bread-title">
      商品展示
    </div>

    <div id="breads-list">
      <ul>
        <li v-for="bread in breadInfo.breads" class="bread">
          <div class="bread-img">
            <img :src="bread.img"/>
          </div>
          <div class="content" @click="show_details(bread)">
            <h3 class="title">{{ bread.name }}</h3>
            <div id='weight-wrapper'>
              <span>净含量：{{ bread.weight }}<small>g/个</small></span>
            </div>
            <div id="price-wrapper">
              <span class="price"><small>￥</small>{{ bread.price }}</span>
            </div>
            <a :href="breadInfo.tel" @click.stop>
              <div class="button">
                预约
              </div>
            </a>
          </div>
        </li>
      </ul>
    </div>
    <bread-details :breadDetails="breadDetails" :site="site" :tel="tel" ref="bread_details"></bread-details>
  </div>

</template>

<script>
// 导入子组件
import breadDetails from './details/details.vue';

export default {
  data() {
    return {
      // 初始化数据
      breadDetails: {},
      site: '',
      tel: ''
    };
  },
  props: {
    breadInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  methods: {
    show_details(bread) {
      this.$refs.bread_details.show();
      // 商品信息
      this.breadDetails = bread;
      // 商品地点
      this.site = this.breadInfo.site;
      // 商家电话
      this.tel = this.breadInfo.tel;
    }
  },
  // 注册子组件
  components: {
    breadDetails
  }
};
</script>

<style style="text/css" lang="less" scoped>
#bread-container {
  padding-left: 10px;
  #bread-title {
    font-size: 18px;
    font-weight: 200;
    color: #333;
    opacity: .5;
    padding: 10px 0px 10px 0;
    border-bottom: 1px solid #DDD8CE;
  }
  #breads-list {
    .bread {
      display: flex;
      position: relative;
      padding: 10px 0;
      &:not(:last-child) {
        border-bottom: 1px solid #DDD8CE;
      }
      .bread-img {
        width: 100px;
        height: 100px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .content {
        flex: 1;
        margin: 0 10px;
        position: relative;
        font-size: 0;
        .title {
          font-size: 17px;
          font-weight: 500;
          color: #111;
          margin-bottom: 5px;
        }
        #weight-wrapper {
          font-size: 16px;
          color: #777;
        }
        #price-wrapper {
          font-size: 16px;
          color: #06c1ae;
        }
        a {
          position: absolute;
          right: 0;
          bottom: 0;
          font-size: 14px;
          color: #06c1ae;
          text-decoration: none;
          .button {
            width: 70px;
            height: 28px;
            line-height: 28px;
            border: 1px solid #06c1ae;
            border-radius: 15px;
            text-align: center;
          }
        }
        /*position: absolute;
        right: 0;*/
      }
    }
  }
}
</style>



