<template>

  <div id="slide-container">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="slide_img in slideImgs">
          <img :src="slide_img"/>
        </div>
      </div>
      <!-- 需要分页器 -->
      <div class="swiper-pagination"></div>
    </div>
  </div>

</template>

<script>
import Swiper from 'swiper';

export default {
  props: {
    slideImgs: {
      type: Array,
      default: function() {
        return [];
      }
    }
  },
  created() {
    this.$nextTick(() => {
      let mySwiper = new Swiper('.swiper-container', {
        autoplay: 5000,   // 自动切换的时间间隔
        speed: 3000,   // 滑动速度
        autoplayDisableOnInteraction: false,   // 用户操作swiper之后自动切换不会停止，每次都会重新启动autoplay
        pagination: '.swiper-pagination',   // 添加分页器
        effect: 'fade',   // 切换效果：淡入
        fade: {
          crossFade: true   // 开启淡出
        }
      });
    });
  }
};
</script>

<style style="text/css" lang="less" scoped>
#slide-container {
  /*padding: 0px 0 5px 0;*/
  .swiper-container {
    .swiper-wrapper {
      .swiper-slide {
        width: 100%;
        height: 200px;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
</style>



