<template>

  <transition name="move">
    <div id="details-container" v-show="show_details">

      <div id="navbar">
        <span class="back-btn" @click="hide">< 返回</span>
        <span class="navbar_title">商品详情</span>
      </div>

      <div id="header">
        <img class="img" :src="breadDetails.img"/>
      </div>

      <div id="content">
        <div class="title">
          <strong>商品信息</strong>
        </div>
        <div class="details">
          <h3 class="name">{{ breadDetails.name }}</h3>
          <div id="price">
            {{ breadDetails.price }}<span>RMB</span>
            <a :href="tel" @click.stop>
              <div class="button">
                预约
              </div>
            </a>
          </div>
          <div class="details-info">
            <span class="key">产品规格：</span>
            <span class="value">{{ breadDetails.weight }}<small>g/个</small></span>
          </div>
          <div class="details-info">
            <span class="key">等级：</span>
            <span class="value">{{ breadDetails.grade }}</span>
          </div>
          <div class="details-info">
            <span class="key">生产日期：</span>
            <span class="value">{{ breadDetails.date }}</span>
          </div>
          <div class="details-info">
            <span class="key">产地：</span>
            <span class="value">{{ breadDetails.production }}</span>
          </div>
        </div>
      </div>

      <split></split>
      <div id="desc">
        <h3 class='title'>商品描述</h3>
        <div class="bread-desc">
          {{ breadDetails.desc }}
        </div>
      </div>

      <split></split>
      <site :site="site"></site>
    </div>
  </transition>

</template>

<script>
// 导入子组件
import split from '../../split/split.vue';
import site from '../../site/site.vue';

export default {
  data() {
    return {
      show_details: false
    };
  },
  props: {
    breadDetails: {
      type: Object,
      default: function() {
        return {};
      }
    },
    site: String,
    tel: String
  },
  methods: {
    // 显示
    show() {
      this.show_details = true;
    },
    // 隐藏
    hide() {
      this.show_details = false;
    }
  },
  components: {
    split,
    site
  }
};
</script>

<style type="text/css" lang='less' scoped>
#details-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background: #fff;
  overflow-y: scroll;
  transform-origin: bottom right;
  &.move-enter-active, &.move-leave {
    transition: all .5s;
    background: #fff;
    transform: rotate(0deg);
  }
  &.move-enter, &.move-leave-active {
    transition: all .5s;
    background: #fff;
    transform: rotate(90deg);
  }
  #navbar {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 45px;
    line-height: 45px;
    text-align: center;
    color: #fff;
    background: #06c1ae;
    .back-btn {
      position: absolute;
      left: 10px;
      font-size: 14px;
    }
    .navbar_title {
      font-size: 16px;
    }
  }
  #header {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
    .img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  #content {
    padding: 10px;
    .title {
      font-size: 17px;
      font-weight: 500;
      color: #fff;
      border-bottom: 1px solid #e8e8e8;
      padding-bottom: 5px;
      strong {
        border-radius: 10px 10px 2px 2px;
        background: #06c1ae;
        padding: 5px 15px;
      }
    }
    .details {
      margin-top: 15px;
      padding-left: 5px;
      .name {
        font-size: 20px;
      }
      #price {
        color: #06c1ae;
        font-size: 30px;
        font-weight: 900;
        height: 50px;
        line-height: 50px;
        span {
          font-size: 14px;
        }
        a {
          float: right;
          font-size: 14px;
          color: #06c1ae;
          text-decoration: none;
          .button {
            width: 70px;
            height: 28px;
            line-height: 28px;
            border: 1px solid #06c1ae;
            border-radius: 15px;
            text-align: center;
          }
        }
      }
      .details-info {
        height: 35px;
        line-height: 35px;
        .key {
          font-size: 15px;
          font-weight: 600;
          color: #07111b;
        }
        .value {
          font-size: 14px;
          color: #999;
        }
      }
    }
  }
  #desc {
    padding: 18px;
    .title {
      margin-bottom: 6px;
    }
    .bread-desc {
      color: #06c1ae;
      font-size: 14px;
      font-weight: 200;
      line-height: 24px;
      padding: 0 8px;
    }
  }
}
</style>



