<template>

  <div id='site-container'>
    <div class="site">
      <span class="name">地点：</span>
      <span class="text">{{ site }}</span>
    </div>
  </div>

</template>

<script>
export default {
  props: {
    site: String,
    default: '暂无地址。'
  }
};
</script>

<style style="text/css" lang="less" scoped>
#site-container {
  .site {
    padding: 16px 12px;
    .name {
      font-size: 16px;
      font-weight: 900;
    }
    .text {
      line-height: 25px;
      color: #07111b;
    }
  }
}
</style>



